'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { clsx } from 'clsx';
import { Rocket, Activity, Globe, Menu, User, LogOut } from 'lucide-react';
import { useState } from 'react';
import { useSession, signOut } from 'next-auth/react';

const navItems = [
    { name: 'Dashboard', href: '/', icon: Activity },
    { name: 'NEO Feed', href: '/feed', icon: Rocket },
    { name: 'Risk Analysis', href: '/risk', icon: Globe },
];

export function Navbar() {
    const pathname = usePathname();
    const [isOpen, setIsOpen] = useState(false);
    const { data: session } = useSession();

    return (
        <nav className="fixed top-0 left-0 right-0 z-50 glass-panel border-b border-white/10">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex items-center justify-between h-16">
                    <div className="flex items-center">
                        <Link href="/" className="flex items-center space-x-3 group relative">
                            <div className="absolute inset-0 bg-white/10 blur-xl rounded-full opacity-0 group-hover:opacity-100 transition-opacity" />
                            <div className="relative p-2.5 rounded-full bg-gradient-to-br from-gray-800 to-black border border-gray-700 shadow-[0_0_15px_rgba(255,255,255,0.1)] group-hover:scale-110 transition-transform z-10">
                                <Rocket className="h-6 w-6 text-white" />
                            </div>
                            <span className="relative z-10 text-2xl font-black tracking-widest text-white drop-shadow-none">
                                ASTROCITY
                            </span>
                        </Link>
                    </div>

                    <div className="hidden md:block">
                        <div className="ml-10 flex items-center space-x-4">
                            {navItems.map((item) => {
                                const Icon = item.icon;
                                const isActive = pathname === item.href;
                                return (
                                    <Link
                                        key={item.name}
                                        href={item.href}
                                        className={clsx(
                                            'flex items-center px-3 py-2 rounded-md text-sm font-medium transition-colors duration-200',
                                            isActive
                                                ? 'bg-white/10 text-cyan-glow shadow-[0_0_10px_rgba(44,182,125,0.2)]'
                                                : 'text-void-gray hover:bg-white/5 hover:text-white'
                                        )}
                                    >
                                        <Icon className="w-4 h-4 mr-2" />
                                        {item.name}
                                    </Link>
                                );
                            })}

                            {session ? (
                                <button
                                    onClick={() => signOut()}
                                    className="flex items-center px-3 py-2 rounded-md text-sm font-medium text-alert-red hover:bg-alert-red/10 transition-colors"
                                >
                                    <LogOut className="w-4 h-4 mr-2" />
                                    Logout
                                </button>
                            ) : (
                                <Link
                                    href="/login"
                                    className="flex items-center px-4 py-2 rounded-md text-sm font-bold bg-cyan-glow text-space-black hover:bg-cyan-glow/80 transition-colors"
                                >
                                    <User className="w-4 h-4 mr-2" />
                                    Login
                                </Link>
                            )}
                        </div>
                    </div>

                    <div className="-mr-2 flex md:hidden">
                        <button
                            onClick={() => setIsOpen(!isOpen)}
                            className="inline-flex items-center justify-center p-2 rounded-md text-void-gray hover:text-white hover:bg-white/10 focus:outline-none"
                        >
                            <Menu className="w-6 h-6" />
                        </button>
                    </div>
                </div>
            </div>

            {isOpen && (
                <div className="md:hidden glass-panel border-t border-white/5">
                    <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3">
                        {navItems.map((item) => (
                            <Link
                                key={item.name}
                                href={item.href}
                                className={clsx(
                                    'block px-3 py-2 rounded-md text-base font-medium',
                                    pathname === item.href
                                        ? 'bg-white/10 text-cyan-glow'
                                        : 'text-void-gray hover:bg-white/5 hover:text-white'
                                )}
                                onClick={() => setIsOpen(false)}
                            >
                                <div className="flex items-center">
                                    <item.icon className="w-4 h-4 mr-2" />
                                    {item.name}
                                </div>
                            </Link>
                        ))}
                        {session ? (
                            <button
                                onClick={() => {
                                    signOut();
                                    setIsOpen(false);
                                }}
                                className="w-full text-left block px-3 py-2 rounded-md text-base font-medium text-alert-red hover:bg-alert-red/10"
                            >
                                <div className="flex items-center">
                                    <LogOut className="w-4 h-4 mr-2" />
                                    Logout
                                </div>
                            </button>
                        ) : (
                            <Link
                                href="/login"
                                className="block px-3 py-2 rounded-md text-base font-medium text-cyan-glow hover:bg-white/5"
                                onClick={() => setIsOpen(false)}
                            >
                                <div className="flex items-center">
                                    <User className="w-4 h-4 mr-2" />
                                    Login
                                </div>
                            </Link>
                        )}
                    </div>
                </div>
            )}
        </nav>
    );
}
